@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.modal{display:flex;justify-content:center;align-items:center}.modal .modal_body{padding:20px;color:#fff;background:gray;border:2px solid #ffc400}.modal .modal_body .input_box{padding:8px;border-radius:5px;outline:none;border:none}.modal .modal_body .button{background:#ffc400;display:block;margin:15px auto 0}body{background-color:#000000;color:#fff;font-family:"Righteous", cursive;margin:0;padding:0}.main{max-width:400px !important;height:97vh}@media (min-width: 700px){.main{border:1px solid #ffc400}}.main h1{text-align:center;color:#ffc400;border-bottom:2px solid white}.main h4{text-align:center}.main .win_msg{animation:popup 1s linear infinite}@keyframes popup{0%{transform:scale(1.2)}50%{transform:scale(1.5)}100%{transform:scale(1.2)}}.main .grid{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:5px}.main .grid .box{position:relative;background-color:#ffc400;width:auto;height:100px;display:flex;justify-content:center;align-items:center}.main .grid .box .icons{font-size:35px;font-weight:bold}.main .grid .box .cross{position:absolute;left:0px;width:100%;height:7px;margin-top:13px}.main .grid .box .horizontal{background:gray}.main .grid .box .vertical{background:gray;transform:rotate(90deg)}.main .grid .box .down_left{background:gray;transform:rotate(-45deg)}.main .grid .box .down_right{background:gray;transform:rotate(45deg)}.main .bottom_div{padding-top:20px;display:flex;justify-content:center}.main .bottom_div .reaload_button{background-color:gray;color:#fff;font-weight:bold}.main .result_div{max-width:400px;padding:10px 0 10px 0;width:100%;position:absolute;bottom:4px;left:50%;transform:translate(-50%, -50%);display:grid;grid-template-areas:"player1 player2";background:gray}.main .result_div .player1{grid-area:player1}.main .result_div .player1 span{border-radius:0 20px 0 0;margin-right:10px}.main .result_div .player2{grid-area:player2}.main .result_div .player2 span{border-radius:20px 0 0 0;margin-left:10px}.main .result_div span{padding:15px;font-weight:bold;font-size:20px;border:2px solid #ffc400;background:#ffc400;color:black}

